@import "../assets/libs/normalize.css/normalize.css";
@import "../assets/libs/reset.css/reset.css";
@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");
.background-image {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.page-wrapper {
  display: flex;
  flex-direction: column;
  margin: 0;
  min-height: 100vh;
}
.page-wrapper_theme_dark {
  background-color: #05061C;
}
.page-wrapper__main {
  flex: 1 1 auto;
}

.container {
  margin: 0 auto;
  padding: 0 0.9375rem;
  max-width: 74.375rem;
}

.header {
  position: relative;
  z-index: 10;
  display: flex;
  align-items: center;
  height: clamp(6.25rem, 5.681947317vw + 5.1136105366rem, 9.375rem);
}
.header__container {
  flex: 1 1 auto;
}
.header__layout {
  position: relative;
  z-index: 2;
  display: flex;
  align-items: flex-start;
}
@media (max-width: 1199.98px) {
  .header__layout {
    align-items: center;
  }
}
.header__logo-wrapper {
  padding: 0;
  padding-left: clamp(0rem, 27.5vw + -20.62465625rem, 3.4375rem);
}
.header__logo-link {
  display: block;
}
.header__nav-wrapper {
  flex: 1 1 auto;
  display: flex;
  justify-content: flex-end;
  padding: 1.0625rem 0 0 0;
}
@media (max-width: 74.99875em) {
  .header__nav-wrapper {
    display: none;
  }
}
.header__burger-wrapper {
  display: none;
}
@media (max-width: 1199.98px) {
  .header__burger-wrapper {
    display: block;
  }
}
.header__switch-wrapper {
  padding: 0.5rem 0.75rem 0 0;
}
@media (max-width: 1199.98px) {
  .header__switch-wrapper {
    flex: 1 1 auto;
    display: flex;
    justify-content: flex-end;
    padding: 0 0.625rem 0 0.625rem;
  }
}
.header__mobile-menu-wrapper {
  position: absolute;
  left: 0;
  top: 0;
}

.responsive-image {
  display: block;
  max-width: 100%;
  height: auto;
}

.nav_vertical .nav__list {
  flex-direction: column;
  align-items: flex-start;
}
.nav_vertical .nav__link {
  padding: 0.9375rem 0;
}
.nav_theme_dark .nav__link {
  color: #FFFFFF;
}
.nav_theme_dark .nav__link:hover {
  color: #E9CF66;
}
.nav__list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}
.nav__link {
  display: block;
  transition: all 200ms ease;
  padding: 0.3125em 0.9375em;
  font-family: Montserrat, sans-serif;
  font-size: 1rem;
  line-height: 1.25em;
  text-decoration: none;
  color: #000000;
}
.nav__link:hover {
  color: #22C3C3;
}

.switch {
  position: relative;
  width: 3rem;
  height: 3rem;
  cursor: pointer;
}
.switch::before {
  content: "";
  position: absolute;
  left: 0.3125rem;
  top: 0.875rem;
  border-radius: 4.75rem;
  width: 2.375rem;
  height: 1.25rem;
  background-color: #0A0B20;
}
.switch::after {
  content: "";
  position: absolute;
  left: 0.4789473684rem;
  top: 1.0414473684rem;
  display: block;
  transition: left 200ms ease;
  border-radius: 50%;
  width: 0.9171052632rem;
  height: 0.9171052632rem;
  background-color: #E9CF66;
}
.switch_turn_on::before {
  background-color: #FFFFFF;
}
.switch_turn_on::after {
  left: 1.6039473684rem;
  background-color: #22C3C3;
}

.burger {
  position: relative;
  width: 3rem;
  height: 3rem;
  cursor: pointer;
}
.burger_cross .burger__line {
  transition: top 100ms ease, transform 100ms ease 100ms;
}
.burger_cross .burger__line_number_1 {
  top: 1.375rem;
  transform: rotate(45deg);
}
.burger_cross .burger__line_number_2 {
  opacity: 0;
  transition: none;
}
.burger_cross .burger__line_number_3 {
  top: 1.375rem;
  transform: rotate(-45deg);
}
.burger_theme_dark .burger__line {
  background-color: #FFFFFF;
}
.burger__line {
  position: absolute;
  left: 0.375rem;
  transition: transform 100ms ease, top 100ms ease 100ms;
  border-radius: 8px;
  width: 2.25rem;
  height: 0.25rem;
  background-color: #000000;
}
.burger__line_number_1 {
  top: 0.875rem;
}
.burger__line_number_2 {
  top: 1.375rem;
  transition: opacity 0s ease 100ms;
}
.burger__line_number_3 {
  top: 1.875rem;
}

.mobile-menu {
  overflow: hidden;
  transition: height 200ms ease-out;
  box-sizing: border-box;
  padding-top: clamp(6.25rem, 5.681947317vw + 5.1136105366rem, 9.375rem);
  width: 100vw;
  height: 0;
  background-color: #FFFFFF;
}
.mobile-menu_open {
  transition: height 200ms ease-in;
  height: 100vh;
}
.mobile-menu_theme_dark {
  background-color: #05061C;
}
.mobile-menu_theme_dark .mobile-menu__scroll-area_shadow {
  box-shadow: 0px 6px 20px 0px rgba(255, 255, 255, 0.25);
}
.mobile-menu__scroll-area {
  overflow: auto;
  width: 100%;
  height: 100%;
}
.mobile-menu__scroll-area_shadow {
  box-shadow: 0px 8px 10px -10px rgba(0, 0, 0, 0.4) inset;
}

.logo_theme_dark .logo__image_dark {
  display: block;
}
.logo_theme_dark .logo__image_light {
  display: none;
}
.logo__image_dark {
  display: none;
}